body{
  margin: auto;
}
#root{
  width: 100%;
}
.wrap-demo{
  background-color: bisque;
  border-bottom: 5px solid #ffffff;
}
.imgx{
  margin: auto;
  display: block;
  width: 100%;
}

.zgh{
  fill: #a9a9a9;
  margin: auto;
  width: 24px;
  height: 24px;
  padding: 0 !important;
  cursor: pointer;
  /* display: inline-block; */
}
.kjh{
  width: 85px;
  display: inline-flex;
  color: #a9a9a9;
  cursor: pointer;
  margin-right: 10px;
}
.kjh:hover,.kjh:hover .zgh{
  color: whitesmoke;
  fill: whitesmoke ;
}
.select-dropdown,
.select-dropdown * {
	margin: 0;
	padding: 0;
	position: relative;
	box-sizing: border-box;
}
.select-dropdown {
	position: absolute;
	background-color: #E6E6E6;
	border-radius: 4px;
  top: -23px;
}
.select-dropdown select {
  font-size: 13px;
  font-weight: normal;
  max-width: 100%;
  padding: 5px 16px 4px 18px;
  border: none;
  background-color: transparent;
  appearance: none;
}
.select-dropdown select:active, .select-dropdown select:focus {
	outline: none;
	box-shadow: none;
}
.select-dropdown:after {
	content: "";
	position: absolute;
	top: 50%;
	right: 8px;
	width: 0;
	height: 0;
	margin-top: -2px;
	border-top: 5px solid #aaa;
	border-right: 5px solid transparent;
	border-left: 5px solid transparent;
}
.whitex,.whitex .zgh{
  color: white;
  fill:white !important;
}